import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, ModalController } from '@ionic/angular';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { PedidosService } from '../../../providers/Api/Pedidos.service';
import { ModalMapasComponent } from '../../../Components/modal-mapas/modal-mapas.component';

@Component({
  selector: 'app-pedidos-inicio',
  templateUrl: './pedidos-inicio.page.html',
  styleUrls: ['./pedidos-inicio.page.scss'],
})
export class PedidosInicioPage implements OnInit {
  configurando = false;
  usuario = '';
  habilitado = false;
  cargado = false;
  id = '';

  pedidos = [];
  role = '';

  constructor(
    private router: Router,
    private alert: AlertController,
    private almacenamiento: InicioService,
    private toast: ToastServices,
    private apiPedidos: PedidosService,
    private modal: ModalController
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.pedidosListar();
  }

  refrescando(event) {
    this.pedidosListar().then(() => {
        event.detail.complete();
    }).catch(() => {
      event.detail.complete();
    })
  }

  


  async pedidosListar() {
  
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

        this.role = info.role;
        this.id = info._id;

        this.usuario = info.nombre;
        this.apiPedidos.misPedido(info.token).subscribe((data: any) => {
           console.log(data)
          if (data.exe) {
           this.pedidos = data.response;
          } else {
            this.toast.toastError('No se pudo consultar los pedidos, inténtelo más tarde');
            this.router.navigate(['/central/menu/'])
          }
          this.cargado = true;
        }, (err) => {
          this.toast.toastError('No se pudo consultar los pedidos, inténtelo más tarde');
          this.router.navigate(['/central/menu/'])
        })
      }
      
  }
 
  async crear() {
    const alert = await this.alert.create({
      header: 'Crear Pedido',
      // tslint:disable-next-line:max-line-length
      message: 'Los pedidos puedes realizarlos para comprar los repuestos o productos que necesites para tu vehículo.',
  
      buttons: [
        {
          text: 'Cancelar',
          cssClass: 'cancelarAlerta',
          role: 'cancel',
          handler: () => {
           
          }
        },
        {
          text: 'Crear',
          cssClass: 'botonalerta',
          handler: (data) => {
            this.router.navigate(['/central/menu/control-mapas'])
          }
        }
      ]
    })

    await alert.present();
  }

  async navegar(id, i) {
    this.pedidos[i].chat = true;
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

        this.apiPedidos.consultarSala(info.token, {pedido: id}).subscribe((data: any) => {
         //  console.log(data)
          if (data.exe) {
            if (data.response.length > 0) {
              this.almacenamiento.insertarChat(data.response[0]).then(() => {
                this.router.navigate(['/central/chats/chat']);
                this.pedidos[i].chat = false;
              }).catch(() => {
                this.pedidos[i].chat = false;
                this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
              })
            } else {
              this.pedidos[i].chat = false;
              this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
            }
            
            this.pedidos[i].chat = false;
          } else {
            this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
            this.pedidos[i].chat = false;
          }
        }, (err) => {
          this.pedidos[i].chat = false;
          this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
        })
      }


   
  }


  info(id) {
    this.router.navigate(['/central/pedidos/info', id])
  }

  async agregar(pedido) {

    const modal = await this.modal.create({
      component: ModalMapasComponent,
      componentProps: {
        pedido
      }
    })

    await modal.present();
  }


  async quitar(id) {

    const alert = await this.alert.create({
      header: 'Atención!',
      // tslint:disable-next-line:max-line-length
      message: '¿Estas seguro de abandonar esta sala? No podras visualizar de nuevo la  información dentro de ella',
  
      buttons: [
        {
          text: 'Cancelar',
          cssClass: 'cancelarAlerta',
          role: 'cancel',
          handler: () => {
           
          }
        },
        {
          text: 'Salir',
          cssClass: 'botonalerta',
          handler: async () => {
            const info = await this.almacenamiento.obtenerToken();

            if ( info ) {
        
                // tslint:disable-next-line:max-line-length
                let nuevo = this.pedidos.filter((item) => {
                  return item._id === id;
                  
                })[0].accesos;

                nuevo = nuevo.map((item) => {
                  return item._id;
                })
        
                nuevo = nuevo.filter((item) => {
                  return item !== info._id;
                })
                console.log(nuevo)
                this.apiPedidos.editarPedido(info.token, {id, accesos: nuevo}).subscribe((data: any) => {
                   // console.log(data)
                    if (data.exe) {
                      this.toast.Especial('Saliste de la sala');
                      this.pedidosListar();
                    } else {
                      this.toast.toastError('No se pudo realizar la petición, inténtelo más tarde');
                    }
                }, (err) => {
                      this.toast.toastError('No se pudo realizar la petición, inténtelo más tarde');    
                })
              }
          }
        }
      ]
    })

    await alert.present();
  
  }



}
